"use client";
import { useState } from "react";
import { useRouter } from "next/navigation";

export default function Login() {
  const [role, setRole] = useState<"council" | "contractor" | "">("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const router = useRouter();

  const handleLogin = async () => {
    if (!role) return alert("Select role first");

    const res = await fetch(`http://localhost:5000/user/login`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ email, password, role }),
    });
    const data = await res.json();
    if (data.success) {
      localStorage.setItem("role", role);
      localStorage.setItem("userId", data.user._id);
      router.push("/dashboard");
    } else alert("Login failed");
  };

  return (
    <div className="h-screen flex flex-col justify-center items-center bg-gray-100">
      <div className="bg-white p-8 rounded shadow w-96">
        <h1 className="text-2xl font-bold mb-4 text-center">Login</h1>
        <div className="flex justify-center mb-4 space-x-4">
          <button
            className={`px-4 py-2 rounded ${role==="council"?"bg-blue-500 text-white":"bg-gray-200"}`}
            onClick={()=>setRole("council")}
          >Council Member</button>
          <button
            className={`px-4 py-2 rounded ${role==="contractor"?"bg-green-500 text-white":"bg-gray-200"}`}
            onClick={()=>setRole("contractor")}
          >Contractor</button>
        </div>
        <input 
          type="email" placeholder="Email" 
          value={email} onChange={(e)=>setEmail(e.target.value)}
          className="border p-2 rounded w-full mb-4" />
        <input 
          type="password" placeholder="Password" 
          value={password} onChange={(e)=>setPassword(e.target.value)}
          className="border p-2 rounded w-full mb-4" />
        <button 
          onClick={handleLogin}
          className="bg-blue-500 text-white w-full py-2 rounded hover:bg-blue-600 transition"
        >Login</button>
      </div>
    </div>
  );
}
